.page-seek-container {
    padding: 0px 300px;
    padding-bottom: 100px;

    display: flex;
    justify-content: space-between;

    background-color: #171717;
}

.btn{
    padding: 10px 25px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    background: transparent;
    outline: none !important;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;

    border: 2px solid rgb(255, 255, 255);
    z-index: 1;
    color: white;

    font-size: 18px;
}

.btn41-43 {
    padding: 10px 25px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    background: transparent;
    outline: none !important;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
}

.btn-41 {
    border: 2px solid rgb(255, 255, 255);
    z-index: 1;
    color: white;
}

.btn-41:after {
    position: absolute;
    content: "";
    width: 0;
    height: 100%;
    top: 0;
    left: 0;
    direction: rtl;
    z-index: -1;
    background: rgb(255, 255, 255);
    transition: all 0.3s ease;
}

.btn-41:hover {
    color: rgb(0, 0, 0);
}

.btn-41:hover:after {
    left: auto;
    right: 0;
    width: 100%;
}

.btn-41:active {
    top: 2px;
}

.btn-next:hover:after{
    left: 0;
    right: auto;
    width: 100%;
}

.btn-prev{
    margin-right: 100px;
}

.current-page-number{
    color: aliceblue;
    margin-right: 125px;
    font-size: 25px;
    text-decoration: overline;
}