.search-input {
    width: 200px;
    height: 35px;

    padding: 0px 10px;

    border-radius: 30px;
    border: 0px;
    outline: grey solid 2px;
}

.navbar {
    margin-left: 20px;

    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-grow: 2;
}

.navbar a {
    color: #5B5B66;
    text-decoration: none;
    font-weight: bold;
    font-size: 16px;
}

.navbar a:hover {
    color: #e2e2e2;
}